body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
}
  
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.App__border {
    border: 1px solid black;
    padding: 10px;
    margin: 10px;
    background-color: goldenrod;
}

.Menu {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    padding-top: 10px;
}

.Menu__item {
    padding: 0 10px;
    cursor: pointer;
}

.Menu__item--select {
    font-weight: bold;
    cursor: default;
}

.Autocomplete__header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}

.Autocomplete__header_label {
    padding-right: 10px;
}
.Autocomplete__header_item {
    padding: 0 10px;
}

.Autocomplite__highlight {
    background-color: #e0e0e0;
}

.Autocomplite__result_item {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}

.Chat__wrapper {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    padding-top: 20px;
}

.Chat__list {
    background-color: #eee;
    min-width: 400px;
}

.Chat__message {
    display: -ms-flexbox;
    display: flex;
}

.Chat__nick {
    width: 100px;
    text-align: right;
    padding-right: 10px;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    overflow: hidden;
}

.Chat__sending {
    position: fixed;
    top: 10px;
    right: 10px;
    width: 70px;
    height: 25px;
    line-height: 25px;
    background-color: green;
}

.Chat_InputGroup {
    display: -ms-flexbox;
    display: flex;
    width: 400px;
    margin-bottom: 20px;
}

.Chat_InputLabel {
    width: 100px;
    text-align: right;
    line-height: 25px;
    padding-right: 10px;
}

.Chat_InputBox {
    -ms-flex-positive: 1;
        flex-grow: 1;
}

.Chat_InputBox * {
    width: 100%;
}



